@import url(https://indestructibletype.com/fonts/Jost.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --card-bg-color: rgba(19, 18, 24, 255);
}

.featured-card {
    background: var(card-bg-color)!important;
    border-color: var(--card-color);
}
.tagline {
    line-height: 2.25rem;
}
.page-header {
    padding-top: 15rem;
    padding-bottom: 15rem;
}

.alternative-section {
    background-color: #1d1d1f;
    color: white;
}
:root {
    --main-text-color: rgba(255, 255, 255, 255);
    --main-text-color-hover: rgba(225, 225, 225, 128);
}

.navbar {
    background: rgba(0, 0, 0, 0.8);
    color: white;
}

.navbar .navbar-link {
    color: rgba(255, 255, 255, 255)!important;
    color: var(--main-text-color)!important;
    font-size: 14px;
}

.navbar .navbar-link:hover {
    color: rgba(225, 225, 225, 128)!important;
    color: var(--main-text-color-hover)!important;
}

.navbar .navbar-brand {
    color: rgba(255, 255, 255, 255)!important;
    color: var(--main-text-color)!important;
}

.navbar .navbar-brand:hover {
    color: rgba(225, 225, 225, 128)!important;
    color: var(--main-text-color-hover)!important;
}
/* "SF Pro Text","SF Pro Icons","Helvetica Neue","Helvetica","Arial",sans-serif */

/* Using Jost font 
 * https://github.com/indestructible-type/Jost/tree/master */

body {
    font-family: 'Jost';
}

h1, h2, h3, h4 {
    font-family: 'Jost';
    font-weight: 500!important;
}

h5, h6 {
    font-family: 'Jost';
    font-weight: 450!important;
}

p {
    font-family: 'Jost';
    font-weight: 300!important;
    font-size: 1.20rem!important;
}
